<template>
    <div class='home'>
      <Swiper :banner="banner" :dataName="'banner'"></Swiper>
 
      <Hot :list="list" :dataName="'home'"></Hot>

      <Show :show="show" :dataName="'show'"></Show>
      <Hot :list="list" :dataName="'home'"></Hot>
      <Show :show="show" :dataName="'show'"></Show>  
      <Hot :list="list" :dataName="'home'"></Hot>
      <Show :show="show" :dataName="'show'"></Show>
      <Tally></Tally>
      <Hot :list="list" :dataName="'home'"></Hot>
    </div>
</template>
<script>
import Swiper from '../components/Swiper'
import Hot from '../components/Hot'
import Show from '../components/Show'
import Tally from '../components/Tally'
export default {
    components:{
       Swiper,Hot,Show,Tally
    },
    data(){
      return{
        list:[],
        banner:[],
        show:[],
      }
    },
    mounted(){
      axios({
        url:'/api/home',
        params:{_page:1,_limit:4}
      }).then(
        res=> 
        // console.log(res.data)
        this.list = res.data.data
      )
      axios({
        url:'/api/banner',
        params:{_page:1,_limit:4}
      }).then(
        res=>
        // console.log(res.data)
        this.banner = res.data.data
      )
      axios({
        url:'/api/show',
        params:{_page:1,_limit:6}
      }).then(
        res=>
        // console.log(res.data)
        this.show= res.data.data
      )
    }
}
</script>
