import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);

import Home from '../pages/Home.vue';
import User from '../pages/User.vue';
import Login from '../pages/Login.vue';
import Reg from '../pages/Reg.vue';
import Tv from '../pages/TV.vue';
import Buycar from '../pages/Buycar.vue';
import Goods from '../pages/Goods.vue';
import GoodsDetail from '../pages/GoodsDetail.vue';
import Recommend from '../pages/Recommend.vue';
import Recreation from '../pages/Recreation.vue';
import Detail from '../pages/Detail.vue';
import ErrorPage from '../components/ErrorPage.vue';

let routes =[
    {path:'/home',component:Home},
    {path:'/login',component:Login},
    {path:'/reg',component:Reg},
    {path:'/recommend',component:Recommend},
    {path:'/recreation',component:Recreation},
    {path:'/tv',component:Tv},
    {path:'/buycar',component:Buycar},
    {path:'/goods',component:Goods},
    {path:'/goodsdetail/:id',component:GoodsDetail,name:'goodsdetail'},
    {path:'/user',component:User},
    {path:'/detail/:id',component:Detail,name:'detail'},
    {path:'/',redirect:"/home"},
    {path:'*',component:ErrorPage},
    
]

let router = new VueRouter({
    mode:'history',
    routes,
    scrollBehavior:(to,from,savePostion)=>{
        return {x:0,y:0}
      }
})

export default router;