<script>
import router from '../plugins/router'
export default {
    data(){
        return {
            user:[]
        }
    },
    methods:{
        logout(){
           axios({
               url:"api/logout",
               method:'put'
           }).then(
               res=>{
                 if(res.data.err==0){
                   this.$router.push('/login')
                 }
               }
            
           )
        }
    },
    beforeRouteEnter(to,from,next){
      axios({
          url:'/api/user'
      }).then(
          res=>{
              if(res.data.err == 0 ){
                next((_this)=>_this.user = res.data.data)
              }else{
                  next('/login')
              }
          }
      )
    }
}
</script>

