<template>
<div>
<Video :tv="tv" :dataName="'tv'"></Video>

<Tally></Tally>
</div>

</template>
<script>
import Video from '../components/Video'
import Tally from '../components/Tally'
export default {
    components:{
Video,Tally
    },
    data(){
        return{
            tv:[]
        }
    },
    mounted(){        
        axios({
            url:'/api/tv',
            params:{_page:1,_limit:6}
        }).then(
            res=>
            this.tv = res.data.data
        )
    }
}
</script>

