<template>
<div class='hot'>
  <Hot :list="list" :dataName="'home'"></Hot>
  <Tlist></Tlist>
  <Tlist></Tlist>
  <Tlist></Tlist>
  <Tlist></Tlist>
  <Tally></Tally>
</div>
</template>
<script>
import Hot from '../components/Hot'
// import Video from '../components/Video'
import Tlist from '../components/Tlist'
import Tally from '../components/Tally'

export default {
    components:{
     Hot,Tlist,Tally
    },
     data(){
      return{
        list:[],
      }
    },
        mounted(){
      axios({
        url:'/api/home',
        params:{_page:1,_limit:4}
      }).then(
        res=> 
        // console.log(res.data)
        this.list = res.data.data
      )
   }
}
</script>