<template>
  <div id="app">
  <Loading v-show="bLoading"></Loading>
  <Header v-show="$store.state.bNav"></Header>
  <!-- <Show></Show> -->
  <transition 
    mode="out-in"
    enter-active-class = "animated bounceInLeft"
    leave-active-class = "animated bounceOutRight"
  >
  <router-view style="animation-delay:50ms"></router-view>
  </transition>

  <!-- <Recreation></Recreation> -->
  <Footer v-show="bFoot"></Footer>
  </div>
 </template>

<script>
// import Show from './components/Show'
import Header from './components/Header'
import Footer from './components/Footer'
import Loading from './components/Loading'

import {mapState} from 'vuex';

export default {
  name: 'app',
  components: {
    Header,Footer,Loading
  },
  computed:mapState(['bFoot','bLoading']),
  watch:{
    $route:{
    handler({path}){
      // let path = to.path;
      console.log(path)

       if(/home|tv|recommend/.test(path)){
       this.$store.commit('VIEW_NAV',true)
       this.$store.commit('VIEW_FOOT',true)
      }
      if(/detail|buycar|goods|goodsdetail|login|reg/.test(path)){
       this.$store.commit('VIEW_NAV',false)
       this.$store.commit('VIEW_FOOT',false)
      }
      if(/user|recreation/.test(path)){
       this.$store.commit('VIEW_NAV',false)
       this.$store.commit('VIEW_FOOT',true)
      }
    },
    imediate:true
    }
  }
}
</script>

